/* Lists
/* ---------------------------------------------------------- */

ul,
ol,
dl {
    margin: 0 0 var(--margin) 0;
}

ol,
ul {
    padding-left: 1.3em;
    padding-right: 1.5em;
    max-width: 100%;

    ol,
    ul {
        margin: 0.5em 0 1em;
    }
}

ul {
    list-style: disc;
}

ol {
    list-style: decimal;
    ol {
        list-style: lower-alpha;
    }
}

li {
    margin: 0.5em 0;
    padding-left: 0.3em;
    line-height: 1.6em;
}

dt {
    display: block;
    font-weight: var(--font-bold);
    margin: 0 0 calc(var(--margin) * 0.5) 0;
}

dd {
    margin-left: var(--margin);
}
